export function getPageNumberView(page) {
    return String(page).padStart(2, '0');
}
export function findDistanceOfArrayItems(array, startItem, endItem) {
    var sortedItemIndexes = [array.indexOf(startItem), array.indexOf(endItem)].sort(function (a, b) { return a - b; });
    var isForward = true;
    var remainderArray = array.slice();
    var forwardLength = remainderArray.slice(sortedItemIndexes[0], sortedItemIndexes[1]).length;
    remainderArray.splice(0, forwardLength);
    var remainderLength = remainderArray.length;
    if (endItem > startItem)
        isForward = false;
    if (forwardLength > remainderLength)
        isForward = !isForward;
    return [!isForward, Math.min(forwardLength, remainderLength)];
}
